
:root {
  --main-background: #E4E9F7;
  --sidebar: #FFF;
  --primary-color: #695CFE;
  --text-color: #707070;
  --primary-color-light: #F6F5FF;
  --dark-mode-section: #dfdede;
    /* --toggle-color: 1 #DDD; */
  --arrow: #8583a0;
  --slider-inner: #ffffff;
  --slider-background: #939393; 
  --icon: #d0cbbf;
}

body.dark-mode {
  --main-background: #18191A;
  --sidebar: #242526;
  --primary-color: #3A3B3C;
  --primary-color-light: 43A3B3C;
  /* --toggle-color: #FFF; */
  --text-color: #ccc;
  --dark-mode-section: #2a2b2b;

  --arrow: #3a3c3b;
  --slider-inner: #181a19;
  --slider-background: #aaaaaa; 
  --icon: #6c747a;

}


body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--main-background);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
