.nav-sidebar {
  position: absolute;
  left: 0;
  bottom: 0;
  top: 0;
  background-color: var(--sidebar);
  width: 200px;
  transition: width 200ms ease-out;
  // overflow: hidden;
  .arrow {
    position: absolute;
    right: 0;
    top: 52px;
    transform: translate(50%);
  }

  &__header {
    display: flex;
    width: 100%;
    padding: 20px 20px;
    box-sizing: border-box;
    border-bottom: 1px solid #525050;
    div {
      width: 50%;
      text-align: center;
    }
  }

  &__items {
    margin: 0;
    padding-left: 0;
  }

  // ======= item =========

  &__item {
    list-style-type:none;
    height: 60px;
  }

  &__item-wrapper {
    display: flex;
    color: var(--text-color);
    text-decoration: none;
    padding: 20px 25px;
    &:hover {
      background-color: #9d9d9d;
    }
  }


  &__item-name {
    margin-left: 20px;
    opacity: 1;
    transition: opacity 200ms;
  }

  &__item-icon {
    color: var(--icon);
    font-size: 15px;
    margin-top: 5px;
  }


  &.is-minimized {
    width: 65px;
    .nav-sidebar__header {
      padding-left: 15px;
    }
  }

  // ========== footer ===========

  &__footer {
    position: absolute;
    bottom: 0;
    width: 100%;
  }

  &__dark-mode {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    background-color: var(--dark-mode-section);
    padding-bottom: 20px;

    p {
      text-align: center;
    }
  }

}