.switch {
  background-color: var(--slider-background);
  height: 20px;
  width: 40px;
  border-radius: 25px;
  border: none;
  position: relative;
  &::after {
    content: '';
    background-color: var(--slider-inner);
    border-radius: 50%;
    height: 15px;
    width: 15px;
    position: absolute;
    top: 50%;
    left: 0px;  
    transform: translate(0, -50%);
    transition: 200ms;
  }
}

.switch.light {
  &::after {
    transform: translate(5px, -50%);
  }
}

.switch.dark {
  &::after {
    transform: translate(20px, -50%);
    
  }
}